@import url(https://fonts.googleapis.com/css2?family=Dongle:wght@300;400;700&family=Nanum+Gothic:wght@400;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Dongle', sans-serif;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

html {
  font-size: 62.5%;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 250ms ease-in;
}

button:active {
  transform: scale(0.9);
}

.toggle {
  position: relative;
  display: inline-block;
  width: 65px;
  height: 32px;
}

.toggle > input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background-color: #9da7b2;
  border-radius: 32px;
}

.slider::before {
  position: absolute;
  content: '';
  height: 24px;
  width: 24px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 300ms;
  border-radius: 50%;
}

